@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Light.woff2") format("woff2"), url("../fonts/Gilroy-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
/*===== global styles =====*/
body.lock {
  overflow: hidden;
}

.nheader,
.regionality {
  --clinicalcenter-black: #000;
  --clinicalcenter-white: #fff;
  --clinicalcenter-bg: #f1faff;
  --clinicalcenter-main1: #00c3bb;
  --clinicalcenter-gray-border: #f5f5f5;
  --clinicalcenter-middleturquoise: #00b0a9;
  --clinicalcenter-darkturquoise: #008d88;
  --text: #777;
  --divivder: #dae2df;
  --border: #f0f0f0;
  font-family: "Gilroy", "Roboto", sans-serif;
  font-size: 16px;
}
.nheader,
.nheader *,
.regionality,
.regionality * {
  box-sizing: border-box;
}
.nheader a,
.regionality a {
  color: inherit;
  text-decoration: none;
  outline: none;
}
.nheader button,
.nheader input,
.regionality button,
.regionality input {
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 0;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
}
.nheader button,
.regionality button {
  cursor: pointer;
}

.lining {
  min-height: 153px;
}
@media (max-width: 1199px) {
  .lining {
    min-height: 68px;
  }
}
@media (max-width: 767px) {
  .lining {
    min-height: 54px;
  }
}

.nheader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: var(--clinicalcenter-bg, #f1faff);
  transition: transform 0.3s ease-out;
}
.nheader.hide {
  transform: translateY(-100%);
}
@media (max-width: 1199px) {
  .nheader {
    position: fixed;
  }
}

.nheader__container {
  width: 100%;
  max-width: 1280px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 1199px) {
  .nheader__container {
    background-color: var(--clinicalcenter-bg, #f1faff);
  }
}

.nheader__logo_small {
  display: none;
  width: 39px;
  height: 40px;
  flex: 0 0 39px;
}
.nheader__logo_small img {
  display: block;
  width: 100%;
  height: auto;
}

.nheader__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 40px;
  padding: 36px 0 18px;
}
@media (max-width: 1199px) {
  .nheader__top {
    z-index: 1;
    gap: 0 23px;
    padding: 18px 0;
  }
}
@media (max-width: 767px) {
  .nheader__top {
    padding: 12px 0;
  }
}

.nheader__logo {
  flex: 0 0 164px;
  width: 164px;
  height: 41px;
}
.nheader__logo picture,
.nheader__logo img {
  display: block;
  width: 100%;
  height: 100%;
}
.nheader__logo img {
  object-fit: contain;
}
@media (max-width: 1199px) {
  .nheader__logo {
    flex: 0 0 128px;
    width: 128px;
    height: 32px;
  }
}
@media (max-width: 767px) {
  .nheader__logo {
    flex: 0 0 119px;
    width: 119px;
    height: 30px;
  }
}

.nheader__location {
  display: flex;
  align-items: center;
  gap: 0 40px;
}
@media (max-width: 1199px) {
  .nheader__location {
    gap: 0 23px;
  }
}
@media (max-width: 767px) {
  .nheader__location {
    display: none;
  }
}

.nheader__select {
  position: relative;
  flex: 0 0 auto;
  padding: 0 19px 0 24px;
  transition: color 0.2s ease-out;
  font-weight: 400;
  font-size: 14px;
  line-height: 110%;
  color: var(--clinicalcenter-black);
}
.nheader__select:before, .nheader__select:after,
.nheader__select span::before,
.nheader__select span::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.2s ease-out;
}
.nheader__select:before, .nheader__select:after {
  left: 0;
}
.nheader__select::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.08673 6.30217C4.24175 1.2248 11.764 1.23066 12.9132 6.30804C13.5874 9.28645 10.1649 12.7536 8.54087 14.3131C7.99978 14.8327 7.99978 14.8347 7.45858 14.3131C5.84038 12.7536 2.41248 9.28059 3.08673 6.30217Z' stroke='%23282828' /%3E%3Cpath d='M7.99958 8.90059C8.89628 8.90059 9.62319 8.17368 9.62319 7.27699C9.62319 6.38029 8.89628 5.65338 7.99958 5.65338C7.10289 5.65338 6.37598 6.38029 6.37598 7.27699C6.37598 8.17368 7.10289 8.90059 7.99958 8.90059Z' stroke='%23282828' /%3E%3C/svg%3E");
}
.nheader__select::after {
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.08673 6.30217C4.24175 1.2248 11.764 1.23066 12.9132 6.30804C13.5874 9.28645 10.1649 12.7536 8.54087 14.3131C7.99978 14.8327 7.99978 14.8347 7.45858 14.3131C5.84038 12.7536 2.41248 9.28059 3.08673 6.30217Z' stroke='%2300c3bb' /%3E%3Cpath d='M7.99958 8.90059C8.89628 8.90059 9.62319 8.17368 9.62319 7.27699C9.62319 6.38029 8.89628 5.65338 7.99958 5.65338C7.10289 5.65338 6.37598 6.38029 6.37598 7.27699C6.37598 8.17368 7.10289 8.90059 7.99958 8.90059Z' stroke='%2300c3bb' /%3E%3C/svg%3E");
}
.nheader__select span {
  display: flex;
  align-items: center;
  gap: 3px;
}
.nheader__select span:before, .nheader__select span:after {
  right: 0;
}
.nheader__select span::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.00002 7.5L6.99398 10.5L10 7.5' stroke='black' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}
.nheader__select span::after {
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.00002 7.5L6.99398 10.5L10 7.5' stroke='%2300c3bb' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}
@media (hover: hover) {
  .nheader__select:hover {
    color: var(--clinicalcenter-main1, #00c3bb);
  }
  .nheader__select:hover::before,
  .nheader__select:hover span::before {
    opacity: 0;
  }
  .nheader__select:hover::after,
  .nheader__select:hover span::after {
    opacity: 1;
  }
}

.nheader__info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
@media (max-width: 1199px) {
  .nheader__info {
    flex-wrap: wrap;
    flex-direction: row;
    gap: 0 15px;
  }
}

.nheader__phone {
  font-weight: 700;
  font-size: 14px;
  line-height: 110%;
  white-space: nowrap;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .nheader__phone:hover {
    color: var(--clinicalcenter-main1, #00c3bb);
  }
}

.nheader__adress {
  font-weight: 300;
  font-size: 14px;
  line-height: 110%;
  color: var(--text, #777);
}

.nheader__search {
  flex: 1 1 auto;
  max-width: 385px;
}
@media (max-width: 1199px) {
  .nheader__search {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -2;
    width: 100%;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0 8px;
    padding: 0 12px 17px;
    background-color: var(--clinicalcenter-bg, #f1faff);
    box-shadow: 0 1px 11px 0 rgba(197, 197, 197, 0.54);
    transition: transform 0.2s ease-out;
  }
  .nheader__search.active {
    transform: translateY(100%);
  }
}

@media (max-width: 1199px) {
  .nheader__search_wrapper {
    width: 100%;
    max-width: 1280px;
    min-width: 320px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 8px;
    padding: 0 15px;
    margin: 0 auto;
  }
}

.nheader__search_inner {
  position: relative;
}
.nheader__search_inner input {
  display: inline-block;
  width: 100%;
  padding: 12px 32px 12px 16px;
  font-weight: 300;
  font-size: 14px;
  line-height: 110%;
  color: var(--clinicalcenter-black, #000);
  background: var(--clinicalcenter-white, #fff);
  border: 1px solid var(--divivder, #dae2df);
  border-radius: 12px;
  transition: border-color 0.2s ease-out;
}
.nheader__search_inner input::placeholder {
  color: var(--text, #777);
}
.nheader__search_inner input:focus {
  border-color: var(--clinicalcenter-main1, #00c3bb);
}
.nheader__search_inner button {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  display: inline-block;
  width: 16px;
  height: 16px;
}
.nheader__search_inner button::before, .nheader__search_inner button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.2s ease-out;
}
.nheader__search_inner button::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 15.5L10.3891 10.8891M12 7C12 10.0376 9.53757 12.5 6.5 12.5C3.46243 12.5 1 10.0376 1 7C1 3.96243 3.46243 1.5 6.5 1.5C9.53757 1.5 12 3.96243 12 7Z' stroke='black' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
.nheader__search_inner button::after {
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 15.5L10.3891 10.8891M12 7C12 10.0376 9.53757 12.5 6.5 12.5C3.46243 12.5 1 10.0376 1 7C1 3.96243 3.46243 1.5 6.5 1.5C9.53757 1.5 12 3.96243 12 7Z' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
@media (hover: hover) {
  .nheader__search_inner button:hover::before {
    opacity: 0;
  }
  .nheader__search_inner button:hover::after {
    opacity: 1;
  }
}
@media (max-width: 1199px) {
  .nheader__search_inner {
    flex: 1 1 auto;
  }
}

.nheader__search_close {
  display: none;
  position: relative;
  flex: 0 0 12;
  width: 12px;
  height: 12px;
}
.nheader__search_close::before, .nheader__search_close::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.2s ease-out;
}
.nheader__search_close::before {
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='13' viewBox='0 0 14 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 0.5L13 12.5' stroke='%23777777' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round' /%3E%3Cpath d='M1 12.5L13 0.5' stroke='%23777777' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
}
.nheader__search_close::after {
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='13' viewBox='0 0 14 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 0.5L13 12.5' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round' /%3E%3Cpath d='M1 12.5L13 0.5' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
}
@media (hover: hover) {
  .nheader__search_close:hover::before {
    opacity: 0;
  }
  .nheader__search_close:hover::after {
    opacity: 1;
  }
}
@media (max-width: 1199px) {
  .nheader__search_close {
    display: inline-block;
  }
}

.nheader__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 1199px) {
  .nheader__nav {
    gap: 0 20px;
  }
}

.nheader__search_toggle {
  position: relative;
  display: none;
  width: 16px;
  height: 16px;
}
.nheader__search_toggle::before, .nheader__search_toggle:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  transition: opacity 0.2s ease-out;
}
.nheader__search_toggle::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 15.5L10.3891 10.8891M12 7C12 10.0376 9.53757 12.5 6.5 12.5C3.46243 12.5 1 10.0376 1 7C1 3.96243 3.46243 1.5 6.5 1.5C9.53757 1.5 12 3.96243 12 7Z' stroke='black' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
.nheader__search_toggle::after {
  opacity: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 15.5L10.3891 10.8891M12 7C12 10.0376 9.53757 12.5 6.5 12.5C3.46243 12.5 1 10.0376 1 7C1 3.96243 3.46243 1.5 6.5 1.5C9.53757 1.5 12 3.96243 12 7Z' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
@media (hover: hover) {
  .nheader__search_toggle:hover::before {
    opacity: 0;
  }
  .nheader__search_toggle:hover::after {
    opacity: 1;
  }
}

.nheader__auth,
.nheader__appointment {
  position: relative;
  transition: color 0.2s ease-out;
  font-weight: 300;
  font-size: 14px;
  line-height: 110%;
}
.nheader__auth::before, .nheader__auth::after,
.nheader__appointment::before,
.nheader__appointment::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity 0.2s ease-out;
}
.nheader__auth:after,
.nheader__appointment:after {
  opacity: 0;
}
@media (hover: hover) {
  .nheader__auth:hover,
  .nheader__appointment:hover {
    color: var(--clinicalcenter-main1, #00c3bb);
  }
  .nheader__auth:hover::before,
  .nheader__appointment:hover::before {
    opacity: 0;
  }
  .nheader__auth:hover::after,
  .nheader__appointment:hover::after {
    opacity: 1;
  }
}
@media (max-width: 1199px) {
  .nheader__auth span,
  .nheader__appointment span {
    display: none;
  }
}

.nheader__auth {
  padding: 12px 7px 12px 28px;
}
.nheader__auth::before, .nheader__auth::after {
  left: 5px;
}
.nheader__auth::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.00049 8.74083C9.80797 8.74083 11.2732 7.23185 11.2732 5.37044C11.2732 3.50903 9.80797 2.00006 8.00049 2.00006C6.19301 2.00006 4.72776 3.50903 4.72776 5.37044C4.72776 7.23185 6.19301 8.74083 8.00049 8.74083Z' stroke='black' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M14.0005 15.0001H2.00049C2.00049 12.3175 4.06715 10.1852 6.66715 10.1852H9.33382C11.9338 10.1852 14.0005 12.3175 14.0005 15.0001Z' stroke='black' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}
.nheader__auth::after {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.00049 8.74083C9.80797 8.74083 11.2732 7.23185 11.2732 5.37044C11.2732 3.50903 9.80797 2.00006 8.00049 2.00006C6.19301 2.00006 4.72776 3.50903 4.72776 5.37044C4.72776 7.23185 6.19301 8.74083 8.00049 8.74083Z' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M14.0005 15.0001H2.00049C2.00049 12.3175 4.06715 10.1852 6.66715 10.1852H9.33382C11.9338 10.1852 14.0005 12.3175 14.0005 15.0001Z' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

.nheader__appointment {
  padding: 12px 8px 12px 38px;
}
.nheader__appointment::before, .nheader__appointment::after {
  left: 14px;
}
.nheader__appointment::before {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.13092 2.5V4.68182M10.8701 2.5V4.68182M2.00049 6.31818H14.0005M13.4787 3.59091C13.7396 3.59091 14.0005 3.86364 14.0005 4.13636V13.9545C14.0005 14.2273 13.7396 14.5 13.4787 14.5H2.52223C2.26136 14.5 2.00049 14.2273 2.00049 13.9545V4.13636C2.00049 3.86364 2.26136 3.59091 2.52223 3.59091H13.4787Z' stroke='black' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
.nheader__appointment::after {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='17' viewBox='0 0 16 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.13092 2.5V4.68182M10.8701 2.5V4.68182M2.00049 6.31818H14.0005M13.4787 3.59091C13.7396 3.59091 14.0005 3.86364 14.0005 4.13636V13.9545C14.0005 14.2273 13.7396 14.5 13.4787 14.5H2.52223C2.26136 14.5 2.00049 14.2273 2.00049 13.9545V4.13636C2.00049 3.86364 2.26136 3.59091 2.52223 3.59091H13.4787Z' stroke='%2300c3bb' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}

@media (max-width: 1199px) {
  .nheader__search_toggle,
  .nheader__auth,
  .nheader__appointment,
  .nheader__burger {
    display: inline-block;
    width: 16px;
    height: 16px;
    padding: 0;
  }
  .nheader__search_toggle::before, .nheader__search_toggle::after,
  .nheader__auth::before,
  .nheader__auth::after,
  .nheader__appointment::before,
  .nheader__appointment::after,
  .nheader__burger::before,
  .nheader__burger::after {
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 767px) {
  .nheader__search_toggle,
  .nheader__auth,
  .nheader__appointment,
  .nheader__burger {
    width: 24px;
    height: 24px;
  }
}

.nheader__burger {
  display: none;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1199px) {
  .nheader__burger {
    display: flex;
  }
}
@media (max-width: 767px) {
  .nheader__burger {
    width: 16px;
    height: 16px;
    transform: scale(1.5);
  }
}
@media (hover: hover) {
