}
@media (hover: hover) {
  .nheader__doctors_label span:hover {
    background-color: var(--clinicalcenter-gray-border, #f5f5f5);
  }
}
@media (max-width: 767px) {
  .nheader__doctors_label {
    white-space: wrap;
  }
}

button.nheader__submit {
  display: block;
  min-height: 50px;
  margin: 56px auto 0;
  padding: 16px 24px 16px 28px;
  color: var(--clinicalcenter-white, #fff);
  border: 1px solid var(--clinicalcenter-white, #fff);
  border-radius: 36px 0 24px 0;
  transition: background-color 0.2s ease-out;
}
@media (hover: hover) {
  button.nheader__submit:hover {
    background-color: var(--clinicalcenter-middleturquoise, #00b0a9);
  }
}
button.nheader__submit:active {
  background-color: var(--clinicalcenter-darkturquoise, #008d88);
}

@media (max-width: 767px) {
  .nheader__services,
  .nheader__doctors {
    display: block;
    min-height: 100%;
    width: 100%;
    border-radius: 0;
    display: none;
  }
  .nheader__services.active,
  .nheader__doctors.active {
    display: block;
  }
}

.nheader__back {
  display: none;
}
@media (max-width: 1199px) {
  .nheader__back {
    position: relative;
    display: block;
    padding: 10px 0 10px 20px;
    transition: color 0.2s ease-out;
  }
  .nheader__back::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    display: inline-block;
    width: 5px;
    height: 10px;
    background-image: url("data:image/svg+xml,%3Csvg width='5' height='8' viewBox='0 0 5 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 1L1 3.99397L4 7' stroke='%23A3A3A3' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition: transform 0.2s ease-out;
  }
}
@media (hover: hover) {
  .nheader__back:hover {
    color: var(--clinicalcenter-main1, #00c3bb);
  }
  .nheader__back:hover::after {
    transform: translateY(-50%) translateX(-5px);
  }
}
@media (max-width: 767px) {
  .nheader__back {
    margin: 0 auto;
  }
}

.nheader__doctors .nheader__back {
  margin: 22px 24px 0;
}
@media (max-width: 767px) {
  .nheader__doctors .nheader__back {
    margin: 22px auto 0;
  }
}